@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  padding: 0 16px;
  margin: auto;
}


.header__container {
  border: 0.1rem solid transparent;
  border-bottom-color: lightgray;
}

.header__logo {
  color: black;
  text-decoration: none;
  font-size: 1.6rem;
  font-weight: 700;
  font-family: Montserrat;
}

.header {
  display: flex;
  justify-content: space-between;
}

.header__theme-toggle {
  border: 0;
  background: none;
  cursor: pointer;
  font-size: 2.4rem;
  display: flex;
  align-items: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.header__theme-input {
  display: none;
}

.header__container--dark {
  background: black;
  border-bottom-color: gray;
}

.header__container--dark .header__logo {
  color: white;
}

.home__info {
  font-size: 1.6rem;
  text-align: center;
  padding: 1.6rem 0;
}

.home__business-card-container {
  display: flex;
  justify-content: center;
  padding: 3.2rem 0;
}
.home__business-cards-container {
  padding-bottom: 3.2rem;
  display: flex;
  justify-content: center;
}

.home--dark {
  background: black;
  color: white;
}

.button {
  font-size: 1.6rem;
  background-color: lightseagreen;
  color: white;
  border: 0;
  border-radius: 0.2rem;
  padding: 1rem;
  cursor: pointer;
  text-decoration: none;
}

.button--loading{
  cursor: wait;
}

.button--danger {
  background-color: red;
}

.business-card-form {
  border: 0.1rem solid lightgray;
  border-radius: 0.2rem;
  padding: 1.6rem 0;
}

.business-card-form__flex {
  display: flex;
  flex-direction: column;
  grid-gap: 1.6rem;
  gap: 1.6rem;
}

.business-card-form__label {
  font-size: 1.6rem;
}

.business-card-form__group {
  grid-gap: 0.8rem;
  gap: 0.8rem;
  display: flex;
  flex-direction: column;
}

.business-card-form__error {
  font-size: 1.6rem;
  color:red;
}

.input {
  font-size: 1.6rem;
  border: 0.1rem solid lightseagreen;
  padding: 0.8rem;
  border-radius: 0.2rem;
}

.input:focus {
  outline: none;
  border-color: seagreen;
  -webkit-filter: drop-shadow(0 0 0.1rem seagreen);
          filter: drop-shadow(0 0 0.1rem seagreen);
}

.business-card {
  border: 0.1rem solid black;
  border-radius: 0.4rem;
  display: inline-flex;
  overflow: hidden;
  flex-wrap: wrap;
  justify-content: center;
  color: black;
  box-shadow: 0px 0px 10px lightgray;
}

.business-card__content {
  font-size: 1.6rem;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 0.8rem;
}

.business-card__image {
  object-fit: contain;
}

.business-card__name {
  font-size: 2.4rem;
  font-weight: bold;
  font-family: Montserrat;
  font-weight: 700;
}

.business-card__username {
  color: gray;
}

.business-card__line {
  height: 0.1rem;
  background-color: black;
  margin: 1rem 0;
}

.business-card__bottomContent {
  line-height: 1.5;
}

.business-card--dark {
  color: white;
}

.business-card--dark .business-card__line {
  background-color: white;
}

.business-cards--dark {
  background: black;
  color: white;
}

.business-cards__button-container {
  display: flex;
  justify-content: flex-end;
  padding: 1.6rem 0;
}

.business-cards__search-label {
  font-size: 1.6rem;
  display: block;
  padding-bottom: 0.8rem;
}

.business-cards__search-container {
  display: flex;
  flex-direction: column;
}

.loader {
  border: 1.6rem solid lightgray;
  border-radius: 50%;
  border-top: 1.6rem solid lightseagreen;
  width: 12rem;
  height: 12rem;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.business-card-list {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  grid-gap: 3.6rem;
  gap: 3.6rem;
  padding: 3.2rem 0;
}

.business-card-list__error {
  font-size: 3.2rem;
  color: lightcoral;
}

.business-card-list__not-found {
  font-size: 3.2rem;
  color: blueviolet;
}

.business-card-list__link {
  text-decoration: none;
}

.business-card-edit--dark {
  background-color: black;
  color: white;
}

.business-card-edit__form {
  padding: 3.2rem 0;
}

.business-card-edit__card {
  padding-bottom: 3.2rem;
  display: flex;
  justify-content: center;
}

.business-card-edit__delete-button {
  padding-top: 3.6rem;
  display: flex;
  justify-content: flex-end;
}

.business-card-edit__loader-container {
  padding-top: 3.6rem;
  display: flex;
  justify-content: center;
}

.modal {
  position: fixed;
  top: 0;
}

.delete-business-card-modal {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.8);
}

.delete-business-card-modal__content {
  background-color: white;
  border-radius: 0.2rem;
  box-shadow: 0 0 5px black;
  padding: 1.6rem;
}

.delete-business-card-modal__title {
  font-size: 3.2rem;
}

.delete-business-card-modal__subtitle {
  font-size: 1.6rem;
}

.delete-business-card-modal__buttons {
  padding: 1.6rem 0;
  display: flex;
  grid-gap: 1.6rem;
  gap: 1.6rem;
}

.delete-business-card-modal__error {
  font-size: 1.6rem;
  color: red;
}

.delete-business-card-modal--dark .delete-business-card-modal__content {
  color: white;
  background-color: black;
}

